.secondary-nav-and-content-wrapper {
  flex-wrap: nowrap;
}

.secondary-left-nav {
  background-color: white;
  border-right: solid 1px $pstg-border-color;
  min-height: calc(100vh - #{$pstg-topbar-height});
  flex: 0 0 $pstg-left-nav-width;
}

.secondary-left-nav-inner {
  position: fixed;
  width: $pstg-left-nav-width - $pstg-border-width;
  height: calc(100vh - #{$pstg-topbar-height});

  header {
    background-color: $pstg-gray-lightest;
    padding: $pstg-padding $pstg-padding 0 $pstg-padding;
    border-bottom: solid 1px $pstg-border-color;
  }

  // Remove all borders that are created by the container
  .list-group-item {
    border-right: 0;
    border-left: 0;

    &:first-child {
      border-top: 0
    }

    &:last-child {
      border-bottom: 0;
    }
  }

  // Make the link as large as the list-group-item
  .list-group-item > a {
    display: block;
    margin: - $pstg-padding;
    padding: $pstg-padding;
  }

}

/*
  Secondary left nav collapsing
  Add the ".secondary-nav-collapsible" class to ".secondary-nav-and-content-wrapper",
  and place a div with ".secondary-nav-collapser" in the appropriate place under ".secondary-left-nav".
*/
$collapse-btn-size-w: 20px; // Collapse button width
$collapse-btn-size-h: 32px; // Collapse button height
$collapse-animation-duration: 0.5s; // Animation duration of expanding/collapsing the list

// Hide stuff until we're in the target width
.secondary-nav-and-content-wrapper.secondary-nav-collapsible {
    .secondary-nav-collapser {
        display: none;
    }
}

@media (max-width: $pstg-sidebar-mobile-mode-width - 1) { // Only show when we're using the mobile sidebar
    .secondary-nav-and-content-wrapper.secondary-nav-collapsible {
        overflow: hidden;

        .secondary-left-nav {
            position: relative;
            z-index: 10;
            transition: margin-left $collapse-animation-duration;
            margin-left: 0px; // We collapse by reducing the margin-left

            .secondary-left-nav-inner {
                max-width: 100%;
            }
        }

        // The sidenav collapse icon
        .secondary-nav-collapser {
            cursor: pointer;
            display: block;
            position: absolute;
            width: $collapse-btn-size-w;
            height: $collapse-btn-size-h;
            background-color: white;
            z-index: 100;
            right: -$collapse-btn-size-w;
            top: calc(50% - #{$collapse-btn-size-h / 2});
            border-top: solid $pstg-border-width $pstg-border-color;
            border-right: solid $pstg-border-width $pstg-border-color;
            border-bottom: solid $pstg-border-width $pstg-border-color;
            border-radius: 0 $pstg-border-radius $pstg-border-radius 0;

            &::after {
                display: block;
                content: '';
                background-image: url('/images/sidebar_collapse_arrow.svg');
                background-repeat: no-repeat;
                width: 13px;
                height: 17px;
                margin-left: 3px;
                margin-top: 6px;
                transition: transform 0.2s;
                transform: rotateY(0deg);
            }
        }

        &.secondary-nav-collapsed {

            .secondary-left-nav {
                margin-left: -$pstg-left-nav-width;
            }

            .secondary-nav-collapser {
                &::after {
                    transform: rotateY(180deg);
                    margin-left: 2px;
                }
            }

        }

    }

    // When the screen gets too small to fit the sidenav, we need to change the position of the collapse
    // icon so it can actually fit in the screen
    @media (max-width: $pstg-left-nav-width + $collapse-btn-size-w) {
        .secondary-nav-and-content-wrapper.secondary-nav-collapsible {
            .secondary-nav-collapser {
                position: fixed;
                right: auto;
                left: 0;
                top: calc(50% - #{$collapse-btn-size-h / 2} + #{$pstg-topbar-height / 2});
            }
        }
    }
}