/*
    .nav-pagination
    Pagination that uses previous/next navigation instead of display a list of individual pages
*/

.nav-pagination.btn-group {
    margin-left: $pstg-padding-thin;

    .btn.btn-secondary {
        min-width: auto;
        padding-left: $pstg-padding-thin;
        padding-right: $pstg-padding-thin;

        &.with-action-icon .pstg-action-icon {
            height: 10px;
            width: 10px; // CLOUD-8349
            top: 8px;
            left: 9px;
        }
    }

    .count-range {
        line-height: 18px;

        .current-count {
            font-weight: 600;
        }

        .total-count {
            color: $pstg-gray-dark;
        }
    }

}