#topbar {
  z-index: 1001;
  position: fixed;
  top: 0;
  height: $pstg-topbar-height;
  width: 100%;
  border-bottom: 1px solid $pstg-gray-light;
  background-color: $pstg-topbar-background-color;

  .page-title {
    display: inline-block;
    padding: 0 0 0 $pstg-padding;
    margin: 0;
    line-height: $pstg-topbar-height;
    font-size: $pstg-font-heading1-size;
    font-weight: $pstg-font-heading1-weight;
  }

}

#topbar-right {
  position: fixed;
  z-index: 1001;
  top: $pstg-topbar-right-margin-y;
  right: $pstg-padding - $pstg-topbar-right-margin-x;
  height: $pstg-topbar-height - 2 * $pstg-topbar-right-margin-y;

  .topbar-item {
    height: 100%;
    display: inline-block;
    float: right;
    padding: 0 $pstg-topbar-right-margin-x;

    + .topbar-item {
      border-right: 1px solid $pstg-gray-light;
    }
  }

  .topbar-icon-with-count {
    display: inline-block;
    position: relative;
    line-height: ($pstg-topbar-height + $pstg-topbar-icon-height) / 2 - $pstg-topbar-right-margin-y;
    padding: 0 $pstg-topbar-right-margin-x / 2;

    .topbar-icon {
      display: inline-block;
      height: 20px;
      vertical-align: bottom;

      svg {
        height: inherit;
      }
    }

    .tag {
      position: absolute;
      top: 1px;
      left: 50%;
      height: 13px;
      min-width: 17px;
      line-height: 13px;
      font-weight: 600;
      font-size: 8px;
      padding: 0 .4em;
      background: white;
      color: $pstg-gray-dark;
      box-shadow: 0 2px 2px rgba(0,0,0,.2);
    }
  }
}

#tab-content {
  margin-top: $pstg-topbar-height;
  position: relative;
  @media(max-width: $pstg-sidebar-mobile-mode-width - 1) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}