@import "_custom";

/*
    .btn-no-hover
    Removes hover style from buttons
*/

@mixin btn-no-hover($category, $color, $bg, $border) {
    .btn-no-hover.btn.btn-#{$category} {
        cursor: default;
        &:hover,
        &:focus,
        &:active {
            color: $color;
            background-color: $bg;
            border-color: $border;
            box-shadow: none;
        }
    }
}

/*
    .btn-group with a <select>
*/
.btn-group .btn.btn-group-select {
    padding: 0 !important; // Keep it flush since we want to use the btn's border instead of the <select>'s
    select.form-control {
        border: none;
    }
}

@include btn-no-hover('primary', $btn-primary-color, $btn-primary-bg, $btn-primary-border);
@include btn-no-hover('secondary', $btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);
@include btn-no-hover('info', $btn-info-color, $btn-info-bg, $btn-info-border);
@include btn-no-hover('warning', $btn-warning-color, $btn-warning-bg, $btn-warning-border);
@include btn-no-hover('danger', $btn-danger-color, $btn-danger-bg, $btn-danger-border);

/*
    .no-results-row
    Table body row for displaying a message that there are no results
*/
table tbody tr.no-results-row {
    vertical-align: middle;
    text-align: center;
    background: $table-bg;

    td {
        padding-top: $pstg-padding-thin;
        padding-bottom: $pstg-padding-thin;
    }

    // Remove hover effects
    &:hover {
        background: $table-bg;
    }

    // Also remove extra styles that may have been applied to the td
    td, td:hover {
        background: $table-bg;
    }
}
