$branding-background-color: #222222;
$menu-background-color: #383838;
$menu-background-selected: #454545;
$menu-text-unselected: #8d8d8d;
$menu-text-selected: #ffffff;
$menu-selection-highlight-bar: #fe5000;
$menu-item-highlighted: #EEE;
$menu-divider-color: #535353;
$sidebar-branding-height: 55px;
$menu-active-tab-animation-duration: 0.3s ease;
$menu-screen-mobile-width: $pstg-sidebar-mobile-mode-width;

// Animation duration and timing used for expanding and collapsing the sidebar
$menu-slide-transition: $pstg-sidebar-slide-transition-duration $pstg-sidebar-slide-transition-timing-function;

/* Global classes */

// Toggles visibility for when the sidebar is collapsed/expanded
// Available globally since they are generally useful
// Note: We treat mobile as always in "expanded" mode
@media(min-width: $menu-screen-mobile-width) {
        #wrapper.toggled .sidebar-expanded-only {
                display: none;
        }
}

#wrapper:not(.toggled) .sidebar-collapsed-only {
        display: none;
}

@media(max-width: $menu-screen-mobile-width - 1) {
        #wrapper .sidebar-collapsed-only {
                display: none;
        }
}

/* Sidebar-specific stylings */
#sidebar-nav {
        margin-top: 0;
        max-height: calc(100vh - #{$sidebar-branding-height}); // Restrict height to what is visible
        padding-bottom: 10px; // Don't have the last link/item completely against the bottom of the page
}

#wrapper {
        padding-left: $pstg-sidebar-expanded-width;

        // Animate the content area in the same way we animate the menu to keep them synchronized when expanding/collapsing
        transition: padding-left $menu-slide-transition;

        &.toggled {
                padding-left: 0;

                #sidebar-wrapper {
                        margin-left: -($pstg-sidebar-expanded-width * 2);
                }
        }
}

#sidebar-wrapper {
        position: fixed;
        z-index: 1005;
        top: 0;
        height: 100vh;
        left: $pstg-sidebar-expanded-width;
        width: $pstg-sidebar-expanded-width;
        margin-left: -$pstg-sidebar-expanded-width;
        background-color: $menu-background-color;
        transition: width $menu-slide-transition, margin-left $menu-slide-transition;

        -webkit-font-smoothing: subpixel-antialiased; // Fixes text looking funny in Safari while the menu animate expands

        // Horizontal divider
        .sidebar-nav-divider {
                height: 0;
                margin: 20px 10px;
                border: 1px inset $menu-divider-color;
        }

        // Dropdown menu for nav sub-links in collapsed mode
        .dropdown-menu {
                top: -3px;
                z-index: 9000; // Really don't want this hiding behind anything
                border-color: $pstg-gray-light;
                left: $pstg-sidebar-collapsed-width - 8px;
                font-size: 14px;

                // Link styling
                .p-tab {
                        color: $pstg-gray-darker;
                        font-size: 14px;
                        display: block; // Make a block so it can be clicked anywhere
                        padding: 0 0 0 20px; // Pad the left side
                        margin: 2px;

                        &.active {
                                font-weight: 700;
                        }

                        &:hover, &:focus {
                                background-color: $pstg-gray-lighter;
                        }
                }
        }

        .sidebar-pill {
                // Common pill styling
                background-color: white;
                color: $menu-text-unselected;
                font-size: 9px;
                font-weight: 600;
                padding: 3px 5px;

                // Pill for the expanded subtab links
                &.subtab-link-pill {
                        position: absolute;
                        top: 3px;
                        right: 20px;
                }

                // Pill for the expanded tab links
                &.tab-link-pill {
                        position: absolute;
                        top: 6px;
                        right: 10px;
                }

                // Pill directly on the tab icon
                &.tab-icon-pill {
                        position: absolute;
                        top: -0.7em;
                        left: 1.7em;
                }
        }

        // Misc links (eg "Help")
        .sidebar-top-level-link {
                padding: 0 28px;
                border-left: none;
                font-size: 13px;
                color: $menu-text-unselected;
                cursor: pointer;
                display: block;

                &:hover, &:focus {
                        color: $menu-text-selected;
                }

                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                display: none;
                        }
                }

        }

        .sidebar-item {
                color: $menu-text-unselected;
                width: $pstg-sidebar-expanded-width;
                border-left: 3px solid transparent;

                position: relative;
                font-size: 14px;
                cursor: pointer;
                display: block;

                // We have two types of transitions we want to use. One is for sliding in/out the bar,
                // and the other is for interaction (hovering, selecting). Due to how we animate the sidebar
                // in on small screens, we need to disable the slide transitions on small screens to prevent double-stacking
                // the animations and causing some ugly effects.
                $item-slide-transition: width $menu-slide-transition;
                $item-style-transition: color $menu-active-tab-animation-duration,
                                           border-color $menu-active-tab-animation-duration,
                                           background-color $menu-active-tab-animation-duration;

                transition: $item-slide-transition, $item-style-transition;

                @media(max-width: $menu-screen-mobile-width - 1px) {
                        & {
                                transition: $item-style-transition; // No slide transition
                        }
                }

                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                width: $pstg-sidebar-collapsed-width;
                        }
                }

                &.active {
                        color: $menu-text-selected;
                        border-color: $menu-selection-highlight-bar;
                        background-color: $menu-background-selected;
                }

                &:hover, &.active:focus, &.active:hover {
                        color: $menu-text-selected;
                }

        }

        .sidebar-item-link {
                display: block;
                width: $pstg-sidebar-expanded-width;
                color: inherit;
                padding: 8px 10px;


                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                width: $pstg-sidebar-collapsed-width;
                        }
                }
        }

        .sidebar-item-icon {
                display: inline-block;
                position: relative;
                padding: 0 5px 0 5px;
                width: 25px;
                margin: 3px 10px 3px 10px;
        }

        .sidebar-item-text {
                position: absolute;
                left: 60px;
                right: 10px; // Expand the area to give us a nice block to work with, like the subtab links
                font-size: 16px;
                margin-left: 0;
                height: 38px;
                color: inherit;
                overflow-x: hidden;
                overflow-y: hidden;

                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                color: transparent;
                        }
                }
        }

        // Group of sub tabs (expanded view only)
        .sidebar-sub-item-group {
                // Cancel the padding of .sidebar-item-link
                position: relative;
                top: -8px;

                padding-top: 5px;
                padding-bottom: 3px;
        }

        // Individual sub tab items (expanded view only)
        .sidebar-sub-item {
                border-left: none;
                font-size: 13px;
                color: $menu-text-unselected;
                padding: 0 2px 2px 60px;
                cursor: pointer;
                display: block;
                white-space: nowrap;
                position: relative;
                overflow: hidden; // Prevent the content from showing through when animating open the sidebar

                a {
                        color: inherit;
                }

                &:hover, &.active, &.active:hover, &.active:focus {
                        color: $menu-text-selected;
                }
        }

        .dropdown-menu-title {
                font-size: 16px;
                font-weight: bold;
                padding: 0 42px 4px 22px;
                cursor: default;
                margin-bottom: 5px;
        }
}


#sidebar-dismiss-overlay {
        z-index: 1003;
        background-color: black;
        opacity: 0.5;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        display: block;

        #wrapper.toggled & {
                display: none;
        }
}

#page-content-wrapper {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;


        @media(min-width: $menu-screen-mobile-width) {
                #wrapper.toggled & {
                        margin-right: -$pstg-sidebar-expanded-width;
                }
        }
}

@media(min-width: $menu-screen-mobile-width) {
        #sidebar-dismiss-overlay {
                z-index: -9999;
                display: none;
                visibility: hidden;
        }

        #page-content-wrapper {
                position: relative;
        }

        #toggle-icon {
                display: none !important;
        }

        #wrapper.toggled {
                padding-left: $pstg-sidebar-collapsed-width;

                #sidebar-wrapper {
                        width: $pstg-sidebar-collapsed-width;
                        margin-left: -$pstg-sidebar-expanded-width;
                }

                #page-content-wrapper {
                        position: relative;
                        margin-right: 0;
                }
        }
}

@media(max-width: $menu-screen-mobile-width - 1px) {
        #sidebar-wrapper {
                .dropdown-menu {
                        display: none !important;
                }
        }
}

// Small screen menu toggle icon/hamburger
#toggle-icon {
        float: left;
        margin-top: 12px;
        margin-left: 8px;
        width: 30px;
        height: 30px;
}

#sidebar-branding {
        width: $pstg-sidebar-expanded-width;
        height: $sidebar-branding-height;
        background-color: $branding-background-color;
        padding: 9px 2px 0 16px;
        margin-bottom: 0;
        cursor: pointer;
        overflow: hidden; // Hide overflow for when we animate from collapsed to expanded
        white-space: nowrap; // Keep as a single line as we animate from collapsed to expanded

        // Disable slide transition on mobile screens. Done in other places as well - see there for details.
        $branding-slide-transition: width $menu-slide-transition;
        $branding-style-transition: padding $menu-slide-transition;
        transition: $branding-slide-transition, $branding-style-transition;

        @media(max-width: $menu-screen-mobile-width - 1px) {
                & {
                        transition: $branding-style-transition; // No slide transition
                }
        }


        @media(min-width: $menu-screen-mobile-width) {
                #wrapper.toggled & {
                        width: $pstg-sidebar-collapsed-width;
                        overflow-x: hidden;
                        padding-left: 8px;
                }
        }

        #sidebar-branding-logo {
                width: 41px;
                height: 35px;
                margin-top: 1px;
                margin-left: 0px;
                margin-bottom: 5px;
        }

        #sidebar-branding-text {
                margin-left: 8px;
                height: 35px;


                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                width: 0;
                                display: none;
                        }
                }
        }

        #sidebar-collapse-arrow {
                margin-left: 43px;
                transition: margin-left $menu-slide-transition;
                height: 35px;
                width: 4px;

                @media(min-width: $menu-screen-mobile-width) {
                        #wrapper.toggled & {
                                margin-left: 3px;

                                // Flip horizontally
                                -moz-transform: scaleX(-1);
                                -o-transform: scaleX(-1);
                                -webkit-transform: scaleX(-1);
                                transform: scaleX(-1);
                                -ms-filter: "FlipH";
                        }
                }
        }

}

// slimScroll support
#wrapper.toggled #sidebar-wrapper > .slimScrollDiv {
        &, #sidebar-nav {
                overflow: visible !important; // Override slimScroll's hidden visibility when collapsed so the popups can still show
        }

        .slimScrollBar {
                display: none !important; // Never show the slimscroll bar when collapsed
        }
}
