.pstg-action-icon {
  fill: $pstg-gray-dark;
}

.pstg-secondary-icon {
  .pstg-alert-warning-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-yellow;
    }
  }

  .pstg-alert-critical-icon,
  .pstg-escalation-icon {
     .pstg-secondary-icon-background {
        fill: $pstg-red;
     }
  }

  .pstg-notification-icon,
  .pstg-in-progress-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-blue;
    }
  }

  .pstg-support-icon .pstg-secondary-icon-background {
    fill: $pstg-orange;
  }

  .pstg-ok-icon .pstg-secondary-icon-background {
    fill: $pstg-green;
  }

  .pstg-paused-icon,
  .pstg-unknown-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-gray-dark;
    }
  }

  &.disabled {
    .pstg-secondary-icon-background {
      fill: $pstg-gray-dark;
    }
  }

  .pstg-secondary-icon-content {
    fill: white;
  }
}

.pstg-sidebar-icon-color {
  //unselected
  .st0 {
    fill:#7E7F7F;
  }
  .st1 {
    fill:#F05323;
  }
  //selected
  &:focus,
  &:active,
  .sidebar-item.active &{
    .st0 {
      fill:#DFDFDE;
    }
    .st1 {
      fill:#F05323;
    }
  }
}

a:hover .pstg-secondary-icon:not(.disabled) {

  .pstg-alert-warning-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-yellow-hover;
    }
  }

  .pstg-alert-critical-icon,
  .pstg-escalation-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-red-hover;
    }
  }

  .pstg-notification-icon,
  .pstg-in-progress-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-blue-hover;
    }
  }

  .pstg-support-icon .pstg-secondary-icon-background {
    fill: $pstg-orange-hover;
  }

  .pstg-ok-icon .pstg-secondary-icon-background {
    fill: $pstg-green-hover;
  }

  .pstg-paused-icon,
  .pstg-unknown-icon {
    .pstg-secondary-icon-background {
      fill: $pstg-gray-dark-hover;
    }
  }
}

a .pstg-secondary-icon-background {
    transition: fill .2s ease;
}

.pstg-action-icon,
.pstg-primary-icon,
.pstg-secondary-icon {
  display: inline-block;
  height: 20px;

  svg {
    height: inherit;
    width: inherit;
    max-height: inherit;
    min-height: inherit;
  }
}

.pstg-circle-icon-background {
  fill: $pstg-gray-light;
}

.pstg-circle-icon-content {
  fill: white;
}

a:hover .pstg-info-circle-icon {
    .pstg-circle-icon-background {
      fill: $pstg-gray-dark;
    }
}

.pstg-checkmark-icon-circle {
  fill: $pstg-green;
}

// alert info icon's two color schemes
.pstg-card-flip-icon .pstg-secondary-icon-content {
  // override the default white content color
  fill: $pstg-gray-light !important;
}

.pstg-card-flip-icon .pstg-secondary-icon-background {
  fill: $pstg-gray-lighter;
}

.pstg-alert-info-icon .pstg-secondary-icon-background {
  fill: $pstg-blue;
}

a:hover {
  .pstg-alert-info-icon:not(.disabled) .pstg-secondary-icon-background {
    fill: $pstg-blue-hover;
  }

  .pstg-card-flip-icon:not(.disabled) .pstg-secondary-icon-background {
    fill: $pstg-gray-lighter-hover;
  }

  .pstg-card-flip-icon .pstg-secondary-icon-content {
    // override the default white content color
    fill: $pstg-gray-light-hover !important;
  }
}
